.main {
  display: flex;
  width: 100vw;
  height: 100vh;
  background-color: #191919;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h2 {
  color: aliceblue;
  text-align: center;
  margin-bottom: 1rem;
  max-width: 75%;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  max-height: 60%;
  max-width: 60%;
}

.content img,
.content iframe {
  max-height: 95%;
  max-width: 100%;
}

.content > a {
  align-self: flex-end;
  margin: 0px;
  color: aliceblue;
  opacity: 30%;
  font-size: 0.7rem;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  margin-top: 1rem;
  max-width: 80%;
}

.nav > button {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 0.3rem;
  font-weight: bold;
  padding: 0.2rem 0.3rem 0.2rem 0.3rem;
}

.nav > button:hover {
  text-decoration: underline;
}
